import * as React from "react"
import { Link, graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { ChevronLeft, ChevronRight, MoreHorizontal } from "lucide-react"

import Bio from "@/components/bio"
import Layout from "@/components/layout"
import Seo from "@/components/seo"
import CategoryTag from "@/components/category-tag"
import CategoryFilter from "@/components/category-filter"
import { Button } from "@/components/ui/button"

const BlogIndex = ({ data, location }: { data: any; location: any }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  // ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò ÏÉÅÌÉú
  const [currentPage, setCurrentPage] = React.useState(1)
  const postsPerPage = 5 // ÌéòÏù¥ÏßÄÎãπ Ìè¨Ïä§Ìä∏ Ïàò
  const totalPages = Math.ceil(posts.length / postsPerPage)

  // ÌòÑÏû¨ ÌéòÏù¥ÏßÄÏóê Ìï¥ÎãπÌïòÎäî Ìè¨Ïä§Ìä∏Îßå Í∞ÄÏ†∏Ïò§Í∏∞
  const startIndex = (currentPage - 1) * postsPerPage
  const endIndex = startIndex + postsPerPage
  const currentPosts = posts.slice(startIndex, endIndex)

  // ÌéòÏù¥ÏßÄ Î≤àÌò∏ ÏÉùÏÑ± Î°úÏßÅ (Ïä§ÎßàÌä∏Ìïú ÌëúÏãú)
  const getPageNumbers = () => {
    const pages = []
    const delta = 2 // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ ÏïûÎí§Î°ú Î≥¥Ïó¨Ï§Ñ ÌéòÏù¥ÏßÄ Ïàò

    // Ï≤´ ÌéòÏù¥ÏßÄÎäî Ìï≠ÏÉÅ ÌëúÏãú
    pages.push(1)

    if (totalPages <= 7) {
      // ÌéòÏù¥ÏßÄÍ∞Ä 7Í∞ú Ïù¥ÌïòÎ©¥ Î™®Îëê ÌëúÏãú
      for (let i = 2; i <= totalPages; i++) {
        pages.push(i)
      }
    } else {
      if (currentPage <= 4) {
        // ÏãúÏûë Î∂ÄÎ∂ÑÏóê ÏûàÏùÑ Îïå
        for (let i = 2; i <= 5; i++) {
          pages.push(i)
        }
        pages.push("...")
        pages.push(totalPages)
      } else if (currentPage >= totalPages - 3) {
        // ÎÅù Î∂ÄÎ∂ÑÏóê ÏûàÏùÑ Îïå
        pages.push("...")
        for (let i = totalPages - 4; i <= totalPages; i++) {
          pages.push(i)
        }
      } else {
        // Ï§ëÍ∞ÑÏóê ÏûàÏùÑ Îïå
        pages.push("...")
        for (let i = currentPage - delta; i <= currentPage + delta; i++) {
          pages.push(i)
        }
        pages.push("...")
        pages.push(totalPages)
      }
    }

    return pages
  }

  if (posts.length === 0) {
    return (
      <Layout location={location} title={siteTitle}>
        <Bio />
        <p>
          Î∏îÎ°úÍ∑∏ Ìè¨Ïä§Ìä∏Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§. "content/blog"
          ÎîîÎ†âÌÜ†Î¶¨(gatsby-config.jsÏóê Ï†ïÏùòÎê®)Ïóê ÌïòÎÇò Ïù¥ÏÉÅÏùò ÎßàÌÅ¨Îã§Ïö¥ ÌååÏùºÏù¥
          ÏûàÏñ¥Ïïº Ìï©ÎãàÎã§.
        </p>
      </Layout>
    )
  }

  return (
    <Layout location={location} title={siteTitle}>
      <Bio />

      {/* Ïπ¥ÌÖåÍ≥†Î¶¨ ÌïÑÌÑ∞ */}
      <CategoryFilter />

      <div className="space-y-8">
        {currentPosts.map((post: any) => {
          const title = post.frontmatter.title || post.fields.slug
          const thumbnail = getImage(post.frontmatter.thumbnail)

          return (
            <article
              key={post.fields.slug}
              className="group bg-card hover:bg-card/80 border rounded-lg shadow-sm hover:shadow-md transition-all duration-200 overflow-hidden"
              itemScope
              itemType="http://schema.org/Article"
            >
              <div className="flex flex-col md:flex-row">
                {/* Ïç∏ÎÑ§Ïùº Ïù¥ÎØ∏ÏßÄ - ÌÅ¥Î¶≠ Í∞ÄÎä• */}
                <Link
                  to={post.fields.slug}
                  className="md:w-96 w-full aspect-video flex-shrink-0 overflow-hidden block rounded-lg"
                >
                  {thumbnail ? (
                    <GatsbyImage
                      image={thumbnail}
                      alt={title}
                      className="w-full h-full object-cover object-center group-hover:scale-105 transition-transform duration-200"
                    />
                  ) : (
                    <div className="w-full h-full bg-muted flex items-center justify-center">
                      <span className="text-muted-foreground text-sm">
                        No Image
                      </span>
                    </div>
                  )}
                </Link>

                {/* ÏΩòÌÖêÏ∏† ÏòÅÏó≠ */}
                <div className="flex-1 p-6 flex flex-col justify-between">
                  <div>
                    <header className="mb-3">
                      {/* Ï†úÎ™© - ÌÅ¥Î¶≠ Í∞ÄÎä• */}
                      <Link to={post.fields.slug}>
                        <h2
                          className="text-xl md:text-2xl font-bold text-foreground group-hover:text-primary hover:text-primary transition-colors line-clamp-2"
                          itemProp="headline"
                        >
                          {title}
                        </h2>
                      </Link>
                      <time className="text-sm text-muted-foreground mt-1 block">
                        {post.frontmatter.date || post.fields?.autoDate}
                      </time>
                    </header>

                    {/* Ïπ¥ÌÖåÍ≥†Î¶¨ÏôÄ ÌÉúÍ∑∏ - ÎèÖÎ¶ΩÏ†ÅÏù∏ ÎßÅÌÅ¨ */}
                    <div className="mb-3">
                      <CategoryTag
                        category={post.frontmatter.category}
                        tags={post.frontmatter.tags}
                        showLinks={true}
                      />
                    </div>

                    <section>
                      <p
                        className="text-muted-foreground leading-relaxed line-clamp-3"
                        itemProp="description"
                      >
                        {post.frontmatter.description ||
                          post.excerpt.replace(/<[^>]*>/g, "")}
                      </p>
                    </section>
                  </div>

                  {/* ÏûêÏÑ∏Ìûà ÏùΩÍ∏∞ Î≤ÑÌäº - ÌÅ¥Î¶≠ Í∞ÄÎä• */}
                  <div className="mt-4">
                    <Link
                      to={post.fields.slug}
                      className="text-primary text-sm font-medium hover:underline inline-block"
                    >
                      ÏûêÏÑ∏Ìûà ÏùΩÍ∏∞ ‚Üí
                    </Link>
                  </div>
                </div>
              </div>
            </article>
          )
        })}
      </div>

      {/* ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò */}
      {totalPages > 1 && (
        <nav className="flex items-center justify-center gap-1 mt-12">
          {/* Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ */}
          <Button
            variant="outline"
            size="sm"
            onClick={() => setCurrentPage(currentPage - 1)}
            disabled={currentPage === 1}
            className="gap-1 pl-2.5"
          >
            <ChevronLeft className="h-4 w-4" />
            Ïù¥Ï†Ñ
          </Button>

          {/* ÌéòÏù¥ÏßÄ Î≤àÌò∏Îì§ */}
          <div className="flex items-center gap-1">
            {getPageNumbers().map((pageNum, index) => {
              if (pageNum === "...") {
                return (
                  <div
                    key={`ellipsis-${index}`}
                    className="flex h-8 w-8 items-center justify-center"
                  >
                    <MoreHorizontal className="h-4 w-4 text-muted-foreground" />
                  </div>
                )
              }

              const isCurrentPage = pageNum === currentPage

              return (
                <Button
                  key={pageNum}
                  variant={isCurrentPage ? "default" : "outline"}
                  size="sm"
                  className="h-8 w-8 p-0"
                  onClick={() => setCurrentPage(pageNum as number)}
                  disabled={isCurrentPage}
                >
                  {pageNum}
                </Button>
              )
            })}
          </div>

          {/* Îã§Ïùå ÌéòÏù¥ÏßÄ */}
          <Button
            variant="outline"
            size="sm"
            onClick={() => setCurrentPage(currentPage + 1)}
            disabled={currentPage === totalPages}
            className="gap-1 pr-2.5"
          >
            Îã§Ïùå
            <ChevronRight className="h-4 w-4" />
          </Button>
        </nav>
      )}
    </Layout>
  )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: [{ frontmatter: { date: DESC } }, { fields: { autoDate: DESC } }]
    ) {
      nodes {
        excerpt
        fields {
          slug
          autoDate(formatString: "YYYYÎÖÑ MMÏõî DDÏùº")
        }
        frontmatter {
          date(formatString: "YYYYÎÖÑ MMÏõî DDÏùº")
          title
          description
          category
          tags
          thumbnail {
            childImageSharp {
              gatsbyImageData(placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
            }
          }
        }
      }
    }
  }
`
